.popup {
  display: none;
  width: 100%;
  background-color: #ffffff;
  box-shadow: 0px 7px 15px 0px rgba(0, 1, 1, 0.15);
}

.popup--error {
  padding-bottom: 23px;
}

.popup__title {
  margin: 0 auto;
  margin-bottom: 35px;
  padding: 0;
  padding-top: 45px;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
}

.popup__message {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 40px;
  padding: 0;
  line-height: 24px;
  font-weight: normal;
  color: #283645;
  text-align: center;
}

.popup--good .popup__wrapper {
  background-color: #f2f2f2;
  padding-top: 18px;
  padding-bottom: 23px;
}

.popup__button {
  display: block;
  margin: 0 auto;
  width: 280px;
  background-color: #2aab6d;
  text-transform: uppercase;
  border-radius: 6px;
  font-size: 18px;
  line-height: 30px;
  padding-top: 16px;
  padding-bottom: 16px;
}

.popup--show {
  display: block;
  position: fixed;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
}

@media (min-width: 660px) {
  .popup {
    width: 462px;
  }

  .popup--error {
    padding-bottom: 27px;
  }

  .popup__title {
    font-size: 26px;
    width: 220px;
  }

  .popup__message {
    width: 320px;
    font-size: 18px;
  }

  .popup__message br {
    display: none;
  }

  .popup--good .popup__wrapper {
    padding-top: 22px;
    padding-bottom: 27px;
  }

  .popup__button {
    width: 300px;
  }
}

@media (min-width: 960px) {
  .popup {
    width: 784px;
  }

  .popup--error {
    padding-bottom: 0;
  }

  .popup__title {
    width: 400px;
  }

  .popup__message {
    width: 400px;
  }

  .popup__wrapper {
    padding-top: 27px;
    padding-bottom: 33px;
  }

  .popup--good .popup__button {
    width: 460px;
  }
}
